/* usage : 

    @media VARIABLE_NAME {
        // style in here 
    }
*/
html {
  box-sizing: border-box;
  font-size: 100%;
}

*, *:before, *:after {
  box-sizing: inherit;
  margin: 0%;
}

body .wc {
  max-width: 1110px;
  width: 100%;
}
body .flexer {
  display: flex !important;
}
@media only screen and (max-width: 1179px) {
  body.MobMenue {
    overflow: hidden;
  }
  body.MobMenue .nlc {
    display: flex !important;
  }
  body.MobMenue .nlc .NavLinks {
    opacity: 1 !important;
    transition-duration: 300ms;
  }
}

#nav {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  padding: 3.4375rem;
  width: 100%;
}
@media only screen and (max-width: 1179px) {
  #nav {
    padding: 2rem 1.5rem;
  }
  #nav .nlc {
    display: none;
  }
}
#nav .flex {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
}
#nav .nlc {
  width: inherit;
}
#nav .NavLinks {
  display: flex;
  flex-flow: row nowrap;
  width: 100%;
  align-items: baseline;
  margin-left: 1rem;
}
@media only screen and (max-width: 620px) {
  #nav .NavLinks {
    z-index: 3;
  }
}
@media only screen and (max-width: 1179px) {
  #nav .NavLinks {
    position: absolute;
    flex-flow: column;
    left: 50%;
    top: 5.5rem;
    transform: translateX(-50%);
    background-color: #3a3053;
    align-items: center;
    border-radius: 1rem;
    padding: 2rem;
    width: 90%;
    margin-left: 0rem;
    opacity: 0;
    transition-duration: 300ms;
    transition-delay: 10ms;
  }
}
#nav .NavLinks a {
  text-decoration: none;
  color: #9e9aa7;
  font-size: 1rem;
  letter-spacing: 0.5px;
  font-weight: 700;
  padding: 0.125rem;
  margin: 0rem 1rem;
  transition-duration: 300ms;
}
@media only screen and (max-width: 1179px) {
  #nav .NavLinks a {
    color: white;
    margin: 0.5rem 1rem;
  }
}
#nav .NavLinks a:nth-child(4) {
  margin-left: auto;
}
@media only screen and (max-width: 1179px) {
  #nav .NavLinks a:nth-child(4) {
    border-top: 1px solid gray;
    width: 100%;
    text-align: center;
    padding-top: 1rem;
  }
}
#nav .NavLinks a:nth-child(5) {
  background-color: #2acfcf;
  padding: 0.5rem 1.25rem;
  border-radius: 1.5rem;
  color: #ffffff;
}
@media only screen and (max-width: 1179px) {
  #nav .NavLinks a:nth-child(5) {
    width: 100%;
    text-align: center;
  }
}
#nav .NavLinks a:nth-child(5):hover {
  opacity: 0.7;
  color: #ffffff;
}
#nav .NavLinks a:hover {
  color: #000000;
  cursor: pointer;
}
#nav .BurgerMenue {
  cursor: pointer;
  display: none;
  margin-top: 0.25rem;
  height: fit-content;
}
@media only screen and (max-width: 1179px) {
  #nav .BurgerMenue {
    display: block;
  }
}
#nav .BurgerMenue .BurgerBars {
  width: 24px;
  height: 3px;
  background-color: #9e9aa7;
}
#nav .BurgerMenue .BurgerBars:nth-child(2) {
  margin: 6px 0px;
}

#hero {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  padding: 3.4375rem;
}
@media only screen and (max-width: 1179px) {
  #hero {
    padding: 2rem 1.5rem;
  }
}
#hero .hero {
  display: flex;
  flex-flow: row nowrap;
  align-items: flex-start;
}
@media only screen and (max-width: 1179px) {
  #hero .hero {
    flex-flow: column nowrap;
    align-items: center;
  }
}
#hero .hero .HeroInfo {
  margin-top: 3.125rem;
  width: 37rem;
  z-index: 2;
}
@media only screen and (max-width: 1179px) {
  #hero .hero .HeroInfo {
    width: 100%;
    text-align: center;
  }
}
#hero .hero .HeroInfo p {
  font-size: 1.5rem;
}
@media only screen and (max-width: 1179px) {
  #hero .hero .HeroInfo p {
    font-size: 1.25rem;
    padding-top: 1rem;
  }
}
#hero .hero .HeroInfo button {
  margin-top: 2rem;
  background-color: #2acfcf;
  color: #ffffff;
  border: none;
  padding: 1rem 3rem;
  border-radius: 2rem;
  font-weight: 700;
  font-size: 1rem;
  letter-spacing: 1px;
  transition-duration: 100ms;
}
#hero .hero .HeroInfo button:hover {
  cursor: pointer;
  opacity: 0.8;
}
#hero .hero img {
  position: absolute;
  height: 480px;
  left: 55%;
}
@media only screen and (max-width: 620px) {
  #hero .hero img {
    width: 130% !important;
    object-position: 4rem !important;
  }
}
@media only screen and (max-width: 1179px) {
  #hero .hero img {
    position: unset;
    width: 60%;
    height: auto;
    object-fit: cover;
    object-position: 0rem;
  }
}

#main {
  margin-top: 12rem;
  background-color: #f0f1f6;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  padding: 0 1rem 7.375rem 1rem;
}
#main .form {
  transform: translateY(-5.25rem);
  margin-bottom: -5.25rem;
}
#main .form form {
  width: 100%;
  height: 168px;
  border-radius: 0.8rem;
  background-color: #3b3054;
  background-image: url(/images/bg-shorten-desktop.svg);
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
  padding: 0rem 3rem;
}
@media only screen and (max-width: 1179px) {
  #main .form form {
    align-items: stretch;
    flex-flow: column nowrap;
    padding: 0rem 1.5rem;
    justify-content: space-evenly;
    background-image: url(/images/bg-shorten-mobile.svg);
    background-repeat: no-repeat;
    background-position: 100% 0%;
  }
}
#main .form form > input {
  height: 64px;
  margin-right: 1rem;
  flex: 0.8;
  border: none;
  border-radius: 0.5rem;
  padding-left: 1rem;
  font-family: inherit;
}
@media only screen and (max-width: 1179px) {
  #main .form form > input {
    flex: unset;
    margin: 0;
    height: 48px;
  }
}
#main .form form > input::placeholder {
  padding: 2rem;
  font-weight: 500;
  font-size: 1rem;
  color: #303030;
  letter-spacing: 1px;
}
#main .form form > button {
  height: 64px;
  flex: 0.2;
  background-color: #2acfcf;
  color: #ffffff;
  font-weight: 700;
  font-size: 1.2rem;
  border: none;
  border-radius: 0.5rem;
  letter-spacing: 1px;
  font-family: inherit;
  transition-duration: 200ms;
}
@media only screen and (max-width: 1179px) {
  #main .form form > button {
    flex: unset;
    height: 48px;
  }
}
#main .form form > button:hover {
  cursor: pointer;
  background-color: #6fd0d0;
}
#main .LinkList {
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
}
#main .LinkList .LinkElement {
  background-color: #ffffff;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  padding: 1rem 1.5rem;
  width: 100%;
  margin-top: 2rem;
  border-radius: 0.5rem;
}
@media only screen and (max-width: 1179px) {
  #main .LinkList .LinkElement {
    align-items: stretch;
    flex-flow: column nowrap;
    padding: unset;
  }
}
#main .LinkList .LinkElement .link {
  text-decoration: none;
  color: #232127;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
@media only screen and (max-width: 1179px) {
  #main .LinkList .LinkElement .link {
    border-bottom: 1px solid #9e9aa7;
    padding: 1rem;
  }
}
#main .LinkList .LinkElement .SLink {
  margin-left: auto;
  margin-right: 1rem;
  text-decoration: none;
  color: #2acfcf;
}
@media only screen and (max-width: 1179px) {
  #main .LinkList .LinkElement .SLink {
    margin: 0;
    padding: 1rem;
  }
}
#main .LinkList .LinkElement .copy {
  background-color: #2acfcf;
  border: none;
  border-radius: 0.5rem;
  font-weight: 700;
  font-size: 1rem;
  color: #ffffff;
  height: 40px;
  flex: 0.1;
  transition: opacity 200ms 0s;
  transition: background-color 200ms 2s;
  padding: 0rem 2rem;
}
@media only screen and (max-width: 1179px) {
  #main .LinkList .LinkElement .copy {
    padding: 1rem 2rem;
    margin: 0px 1rem 1rem 1rem;
  }
}
#main .LinkList .LinkElement .copy:hover {
  cursor: pointer;
  opacity: 0.6;
  transition: opacity 200ms 0s;
  transition: background-color 200ms 2s;
}
#main .LinkList .LinkElement .copy:active {
  background-color: #3b3054;
  content: "Copied!";
  transition: none;
}
#main .StatInfo {
  margin-top: 7.5rem;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
}
#main .StatInfo > h2 {
  font-weight: 700;
  color: #3b3054;
  font-size: 2rem;
}
@media only screen and (max-width: 1179px) {
  #main .StatInfo > h2 {
    font-size: 1.8rem;
    text-align: center;
  }
}
#main .StatInfo > p {
  margin-top: 1rem;
  color: #9e9aa7;
  width: 50%;
  text-align: center;
}
@media only screen and (max-width: 1179px) {
  #main .StatInfo > p {
    width: auto;
    text-align: center;
  }
}
#main .StatInfo > .Features {
  display: flex;
  flex-flow: row nowrap;
  margin-top: 6rem;
  align-items: center;
  height: 355px;
}
@media only screen and (max-width: 1179px) {
  #main .StatInfo > .Features {
    flex-flow: column nowrap;
    height: auto;
  }
}
#main .StatInfo > .Features .FeatureBox {
  height: 267px;
  background-color: #ffffff;
  border-radius: 1rem;
  padding: 33px;
  max-width: 350px;
}
@media only screen and (max-width: 1179px) {
  #main .StatInfo > .Features .FeatureBox {
    height: auto;
    padding: 10%;
  }
}
#main .StatInfo > .Features .FeatureBox .icon {
  height: 88px;
  width: 88px;
  background-color: #3b3054;
  border: none;
  border-radius: 20rem;
  transform: translateY(-5rem);
  margin-bottom: -3rem;
}
@media only screen and (max-width: 1179px) {
  #main .StatInfo > .Features .FeatureBox .icon {
    transform: translate(-50%, -5rem);
    position: relative;
    left: 50%;
  }
}
#main .StatInfo > .Features .FeatureBox .icon img {
  position: relative;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
#main .StatInfo > .Features .FeatureBox p {
  margin: 1rem 0rem;
}
#main .StatInfo > .Features .FeatureBox:first-child {
  align-self: start;
}
#main .StatInfo > .Features .FeatureBox:last-child {
  align-self: end;
}
#main .StatInfo > .Features .spacer {
  background-color: #2acfcf;
  width: 30px;
  height: 8px;
}
@media only screen and (max-width: 1179px) {
  #main .StatInfo > .Features .spacer {
    width: 8px;
    height: 92px;
  }
}

body {
  font-family: "Poppins", sans-serif;
}

h1, h2, h3 {
  margin-top: 0px;
}

h1 {
  color: #232127;
  font-size: 5rem;
  font-weight: 700;
  line-height: 6rem;
}
@media only screen and (max-width: 1179px) {
  h1 {
    font-size: 3rem;
    line-height: 3.5rem;
  }
}

p {
  color: #9e9aa7;
}